@import './Global.scss';


/* #region ========================= BASE ========================= */

.header * {
    margin: 0;
    padding: 0;
}

.header {
    // border: 02px solid #ff0000;

    background: #000000;

    color: #ffffff;
    line-height: 55px;
    text-align: left;

    width: 100vw;
    height: $header-height;

    // margin: 0;
    // padding: 0;

    position: fixed;
    z-index: 999;

    /* #region -------------------- hed -------------------- */

    .hed {
        // border: 02px solid #0000ff;

        width: $body-width;
        min-width: $body-min-width;
        max-width: $body-max-width;

        height: inherit;

        margin: 0 auto;
        // padding: inherit;

        position: relative;

        display: block;

        .hed01 {
            // border: 02px solid #ff00ff;

            font-size: 30px;

            width: auto;
            height: inherit;

            // margin: 0;
            // padding: inherit;

            // position: inherit;
            // top: 10px;
            // left: 50px;

            position: absolute;
            bottom: -17.5%;
            left: 35px;

            // display: table-cell;
        }

        .hed02 {
            // border: 02px solid #00ffff;

            font-size: 20px;

            width: auto;
            height: inherit;

            // margin: 0;
            // padding: inherit;

            // position: inherit;
            // top: 9.5px;
            // left: 60px;

            position: absolute;
            bottom: -22.5%;
            left: 310px;

            display: table-cell;
        }

        .contact {
            // border: 02px solid #ffff00;

            font-size: 18px;
            letter-spacing: .05em;

            position: absolute;
            // top: 9.5px;
            // left: 159px;
            // left: 140px;
            bottom: -17.5%;
            right: 0;
            // right: -80px;

            // margin: 0;
            // padding: inherit;

            display: table-cell;

            a, a:visited {
                color: #ffffff;
            }
        }

        .logoIcon {
            // border: 02px solid #ff0000;

            // width: 150px;
            height: 125px;

            // margin: 0;
            // padding: 0;

            // position: relative;
            // top: -56px;
            // left: -100px;
            // z-index: 99;

            position: absolute;
            top: 7.5px;
            left: -120px;
            z-index: 99;

            display: block;
            overflow: visible;

            // cursor: pointer;

            // -------------------- logoIcon / afro --------------------

            #shigeru_logo_afro_k {
                // fill: #00ff00;

                clip-path: url(#shigeru_logo_afro_k_mask);
                -webkit-clip-path: url(#shigeru_logo_afro_k_mask);
            }

            #shigeru_logo_afro_w {
                // fill: #0000ff;
                fill: #ffffff;

                clip-path: url(#shigeru_logo_afro_w_mask);
                -webkit-clip-path: url(#shigeru_logo_afro_w_mask);
            }

            // -------------------- logoIcon / glasses --------------------

            #shigeru_logo_glasses_k {
                // fill: #00ff00

                clip-path: url(#shigeru_logo_glasses_k_mask);
                -webkit-clip-path: url(#shigeru_logo_glasses_k_mask);
            }

            #shigeru_logo_glasses_w {
                // fill: #0000ff;
                fill: #ffffff;

                clip-path: url(#shigeru_logo_glasses_w_mask);
                -webkit-clip-path: url(#shigeru_logo_glasses_w_mask);
            }

            // -------------------- logoIcon / kanji --------------------

            #shigeru_logo_kanji_w {
                // fill: #ff00ff;
                fill: #ffffff;

                // clip-path: url(#header_mask);
                clip-path: url(#shigeru_logo_kanji_w_mask);
                -webkit-clip-path: url(#shigeru_logo_kanji_w_mask);
            }
        }
    }

    /* #endregion -------------------- hed -------------------- */


    /* #region -------------------- navBarContainer -------------------- */

    .navBarContainer {
        // border: 2px solid #00ff00;
        // border-top: 1px solid #cccccc;
        border-bottom: 1px solid #cccccc;

        // background: #ffffff;
        @include navbgGradient01();

        width: $body-width;
        min-width: $body-min-width;
        max-width: $body-max-width;

        height: $navBar-height;
        // height: 30px;

        margin: 0 auto;
        // padding: 0;

        // position: absolute;
        // display: table;


        /* #region -------------------- workNav -------------------- */

        .workNav {
            // border: 1px solid #ff00ff;

            // background: #ffffff;
            @include navbgGradient01();

            // background: #eeeeee;
            // background: #ffffff;

            text-align: center;

            width: $body-width;
            min-width: $body-min-width;
            max-width: $body-max-width;

            height: $workkNav-height;

            margin: 0 auto;
            // padding: 0;
            padding: 5px 0 0 0;

            // position: fixed;
            position: absolute;
            // top: -40px;
            // top: -55px;
            // top: 115px;
            // top: -$navBar-height;
            top: $navBar-height;
            // top: $header-height + $navBar-height;
            // top: -($header-height - $navBar-height);
            left: 0;
            right: 0;
            z-index: -1;

            .employerIcon {
                // border: 1px solid #cccccc;
                // border-right: 1px solid #cccccc;

                // @include navbgGradient03();
                // mix-blend-mode: multiply;

                width: 10.875%;
                height: 100%;

                display: inline-block;
                vertical-align: middle;

                // margin: 0;
                // padding: 0;

                position: relative;
                z-index: 1;

                img {
                    // border: 3px solid #ff0000;

                    max-width: 65%;
                    // max-height: 85%;
                    max-height: 80%;

                    margin: auto;

                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                }
            }

            /* #region - - - - - - - - - - - Ferda A separators... - - - - - - - - - - - */

            .employerIcon:before {
                border-left: 0.5px solid #cccccc;
                border-right: 0.5px solid #cccccc;

                content: "";
                width: 100%;
                height: 40%;

                position: absolute;
                left: 0;
                right: 0;
                top: 30%;
                bottom: 0;
            }

            /* #endregion - - - - - - - - - - - Ferda A separators... - - - - - - - - - - - */
        }

        /* #endregion -------------------- workNav -------------------- */


        /* #region -------------------- navBar -------------------- */

        .navBar {
            // border: 2px solid #0000ff;
            // border-top: 1px solid #cccccc;
            // border-bottom: 1px solid #cccccc;

            // background: #ffffff;
            // background: none;
            @include navbgGradient01();
            // @include navbgGradient02();

            // color: #ffffff;
            color: #000000;
            font-size: 1.125em;
            text-align: center;

            // width: 70%;
            width: 50%;
            height: $navBar-height;

            margin: 0 auto;
            // padding: 0;

            display: grid;
            // grid-template-columns: repeat(6, 16.666666666666667%);
            // grid-template-columns: repeat(6, auto);
            grid-template-columns: repeat(5, auto);
            column-gap: 0;
            align-items: center;

            // position: absolute;
            // top: $header-height;

            // A {
            .navItem {
                // border: 2px inset #ffff00;
                // border-right: 1px solid #cccccc;

                // background: none;

                color: inherit;
                text-decoration: none;

                height: inherit;

                // margin: 0;
                // padding: 0;

                display: flex;
                align-items: center;
                justify-content: center;

                /* #region - - - - - - - - - - - A: link behavior - - - - - - - - - - - */

                // :link {
                //     color: #00ff00;
                // }

                // :visited {
                //     color: #ff00ff;
                // }

                // :hover {
                //     color: #ff0000;
                // }

                // :active {
                //     background: #eeeeee;
                //     color: #00ff00;
                // }

                /* #endregion - - - - - - - - - - - A: link behavior - - - - - - - - - - - */
            }

            .activeNavDiv {
                // border: 2px inset #00ffff;

                // @include navbgGradient01();
                // @include navbgGradient02();
                @include navbgGradient03();
                // mix-blend-mode: multiply;

                color: #ff0000;
                text-align: center;

                // width: auto;
                // height: $navBar-height;
                height: inherit;

                position: absolute;
                // top: $header-height;
                left: 0;
                // left: 50vw;
                // z-index: -1;
            }

            /* #region - - - - - - - - - - - Ferda A separators... doesn't work here - - - - - - - - - - - */

            // // A:before {
            // .navItem:before {
            // // .navItem:after {
            //     border: 2px inset #ff0000;
            //     // border-left: 0.5px solid #cccccc;
            //     // border-right: 0.5px solid #cccccc;

            //     content: "";
            //     width: 100%;
            //     height: 40%;

            //     position: absolute;
            //     left: 0;
            //     right: 0;
            //     // top: 30%;
            //     top: $header-height + $navBar-height/3;
            //     bottom: 0;
            // }

            /* #endregion - - - - - - - - - - - Ferda A separators... doesn't work here - - - - - - - - - - - */
        }

        /* #endregion -------------------- navBar -------------------- */
    }

    /* #endregion -------------------- navBarContainer -------------------- */
}

/* #endregion ========================= BASE ========================= */



/* #region ========================= DESKTOPS: 1441px+ ========================= */

@media  
    only screen and (min-device-width: 1441px) 
    { 
        .navBurger {
            display: none;
        }
    }

/* #endregion ========================= DESKTOPS: 1441px+ ========================= */



