@media  
    // only screen and (min-device-width: 992px) 
    // only screen and (min-device-width: 992px), 
    only screen and (max-device-width: 1440px) 
    { 
        .header {
            // border: 02px solid #ff0000;

            /* #region -------------------- hed -------------------- */

            .hed {
                // border: 02px solid #0000ff;

                // left: -20px !important;

                .contact {
                    // border: 02px solid #ffff00;

                    right: -7.5vw !important;
                }
            }

            /* #endregion -------------------- hed -------------------- */

