/* #region ========================= MOBILE: 375px ========================= */

@media  
    // only screen and (min-device-width: 375px) 
    // only screen and (min-device-width: 375px), 
    only screen and (max-device-width: 767px) 
    { 
        .header {
            // border: 02px solid #ff0000;

            height: 50px;

            /* #region -------------------- hed -------------------- */

            .hed {
                // border: 02px solid #0000ff;

                // left: 0 !important;

                .hed02, .contact {
                // .hed01, .hed02, .contact {
                    display: none;
                }

                .hed01 {
                    font-size: 1.625em;
                    bottom: -5%;
                    left: 26.25vw;
                }

                .logoIcon {
                    height: 100px;

                    top: 7.5px;
                    left: -15px;
                }
            }

            /* #endregion -------------------- hed -------------------- */


            /* #region -------------------- NAV -------------------- */

            .navBarContainer {
                // border: 1px solid #ff0000;
                border: none;
                background: none;

                font-size: 0.75em;

                width: 100vw;
                min-width: 100vw;
                max-width: 100vw;

                height: 6.25vh;

                // margin: -50px 0 0 0;
                // top: 0;

                // display: none;

                .navBar {
                    // border: 1px solid #00ff00;
                    background: none;

                    font-size: 1.75em;

                    // width: 80%;
                    width: 100%;
                    height: inherit;

                    // top: inherit;
                    // top: 0;
                    // right: 0;

                    grid-template-columns: repeat(1, auto);

                    .navItem {
                        // border: 1px solid #0000ff;
                        border-bottom: 1px solid #cccccc;

                        @include navbgGradient01();

                        // width: 20%;

                        // margin: 0;
                        // padding: 0;
                    }

                    .activeNavDiv {
                        // border: 1px solid #ff00ff;
                        @include navbgGradient04();

                        // width: inherit !important;
                    }
                }
            }

            .navBurger {
                // border: 1px solid #00ff00;
                // background: rgba(0,0,0,0.5);

                width: 50px;
                // height: $navBar-height;
                height: 95%;

                // margin: 0;
                // margin: $header-height 0 0 0;
                // padding: 0;

                position: absolute;
                top: 5%;
                right: 0;
                z-index: 9999;

                display: table-cell;
                // display: none;

                .navDrawer {
                    // border: 1px solid #00ff00;
                    // background: rgba(0,0,0,0.5);

                    width: inherit;

                    // margin: 0;
                    margin: $navBar-height 0 0 0;
                    // padding: inherit;

                    position: inherit;
                    right: inherit;

                    img {
                        // border: 1px solid #0000ff;
                        // background: rgba(255,255,255,0.5);
                        background: rgba(255,0,0,0.25);

                        width: 30%;

                        // margin: 0;
                        padding: 10px;
                    }
                }
            }

            /* #endregion -------------------- NAV -------------------- */
        }
    }

/* #endregion ========================= MOBILE: 375px ========================= */
